<template>
    <CCard class="p-4">
        <CCardBody>
            <form v-on:submit.prevent>
                <h4>Create Rent</h4>
                <hr />
                <v-text-field
                    v-model="owner"
                    label="Owner"
                    :error="errors.owner ? true : false"
                    :error-messages="errors.owner"
                    required
                    outlined
                    dense
                    clearable
                ></v-text-field>
                <v-text-field
                    v-model="tenant"
                    label="Tenant"
                    :error="errors.tenant ? true : false"
                    :error-messages="errors.tenant"
                    required
                    outlined
                    dense
                    clearable
                ></v-text-field>
                <v-text-field
                    v-model="property"
                    label="Property"
                    :error="errors.property ? true : false"
                    :error-messages="errors.property"
                    required
                    outlined
                    dense
                    clearable
                ></v-text-field>
                <v-text-field
                    v-model="amount"
                    label="Amount"
                    type="number"
                    :error="errors.amount ? true : false"
                    :error-messages="errors.amount"
                    required
                    outlined
                    dense
                    clearable
                ></v-text-field>
                <v-row>
                    <c-col md="4" sm="4">
                        <v-menu
                            v-model="startDateMenu"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            transition="scale-transition"
                            offset-y
                            min-width="auto"
                        >
                            <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                    v-model="startDate"
                                    label="Start Date"
                                    outlined
                                    dense
                                    clearable
                                    readonly
                                    :error="errors.start_date ? true : false"
                                    :error-messages="errors.start_date"
                                    v-bind="attrs"
                                    v-on="on"
                                ></v-text-field>
                            </template>
                            <v-date-picker
                                v-model="startDate"
                                @input="startDateMenu = false"
                            ></v-date-picker>
                        </v-menu>
                    </c-col>
                    <c-col md="4" sm="4">
                        <v-menu
                            v-model="fixTermTenancyDateMenu"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            transition="scale-transition"
                            offset-y
                            min-width="auto"
                        >
                            <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                    v-model="fixTermTenancyDate"
                                    label="Fix Term Tenancy Date"
                                    outlined
                                    dense
                                    clearable
                                    readonly
                                    :error="
                                        errors.fix_term_tenancy_date
                                            ? true
                                            : false
                                    "
                                    :error-messages="
                                        errors.fix_term_tenancy_date
                                    "
                                    v-bind="attrs"
                                    v-on="on"
                                ></v-text-field>
                            </template>
                            <v-date-picker
                                v-model="fixTermTenancyDate"
                                @input="fixTermTenancyDateMenu = false"
                            ></v-date-picker>
                        </v-menu>
                    </c-col>
                    <c-col md="4" sm="4">
                        <v-menu
                            v-model="breakClauseDateMenu"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            transition="scale-transition"
                            offset-y
                            min-width="auto"
                        >
                            <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                    v-model="breakClauseDate"
                                    label="Break Clause Date"
                                    outlined
                                    dense
                                    clearable
                                    readonly
                                    :error="
                                        errors.break_clause_date ? true : false
                                    "
                                    :error-messages="errors.break_clause_date"
                                    v-bind="attrs"
                                    v-on="on"
                                ></v-text-field>
                            </template>
                            <v-date-picker
                                v-model="breakClauseDate"
                                @input="breakClauseDateMenu = false"
                            ></v-date-picker>
                        </v-menu>
                    </c-col>
                </v-row>
                <hr />
                <CButton @click="submit" color="primary" class="px-4">
                    Submit
                    <v-progress-circular
                        v-if="loading"
                        indeterminate
                        color="primary"
                        :size="15"
                    ></v-progress-circular>
                </CButton>
            </form>
        </CCardBody>
    </CCard>
</template>
<script>
//

export default {
    name: "CreateRent",
    data() {
        return {
            owner: "",
            tenant: "",
            property: "",
            amount: "",
            startDate: "",
            startDateMenu: false,
            breakClauseDate: "",
            breakClauseDateMenu: false,
            fixTermTenancyDate: "",
            fixTermTenancyDateMenu: false,
            errors: {},
            loading: false
        };
    },
    methods: {
        submit() {
            let self = this;
            if (self.loading) {
                return;
            }
            self.loading = true;
            let data = {
                owner: self.owner,
                tenant: self.tenant,
                property: self.property,
                amount: self.amount,
                start_date: self.startDate,
                fix_term_tenancy_date: self.fixTermTenancyDate,
                break_clause_date: self.breakClauseDate
            };
            this.$store
                .dispatch("rent/create", data)
                .then(response => {
                    self.loading = false;
                    self.$router.back();
                })
                .catch(error => {
                    self.loading = false;
                });
        }
    }
};
</script>

<style scoped>
.v-text-field >>> input {
    font-size: 0.8em;
    font-weight: 100;
}
.v-text-field >>> label {
    font-size: 0.8em;
}
.v-text-field >>> button {
    font-size: 0.8em;
}
</style>
